body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
nav{
  display: flex;
}
li{
  list-style-type: none;
  margin: 0 6px;
}
li a{
  text-decoration: none;
  color: rgb(87, 160, 255);
}
html {
  margin: 0;
  padding: 0;
  background: #17181c;
  color: white;
  box-sizing: border-box;
  text-decoration: none;
  width: 100%;
  height: 100%; }

.header {
  width: 100%;
  padding: 15px 0;
  text-align: center;
  border-bottom: 1px solid gray;
  height: 111px;
  background: linear-gradient(45deg, rgba(71, 182, 156, 0.589), #bbb12c); }
  .header h1 {
    margin: 0; }
  .header__input {
    max-width: 270px;
    width: 100%;
    border: none;
    background: #17181c;
    padding: 10px;
    color: white;
    margin: 15px 0;
    outline: none;
    font-size: 16px; }
    @media all and (max-width: 479px) {
      .header__input {
        max-width: 210px; } }
  .header__submit {
    width: 69px;
    background: #30333b;
    color: white;
    border: none;
    padding: 10px;
    outline: none;
    font-size: 16px;
    cursor: pointer; }

.list {
  width: 100%;
  display: flex;
  justify-content: center; }
  .list ul {
    list-style-type: none;
    padding: 0;
    margin: 9px 0;
    max-width: 360px;
    width: 100%; }
  .list-item {
    position: relative;
    padding: 6px 12px 6px 36px;
    color: white;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    cursor: pointer;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none; }
    .list-item__done {
      position: absolute;
      border-color: #5e5e5e;
      border-style: solid;
      border-width: 1px;
      border-radius: 50%;
      top: 5px;
      left: 6px;
      height: 24px;
      width: 24px; }
      .list-item__done:hover::before {
        content: "";
        position: absolute;
        border-color: #00e93a;
        border-style: solid;
        border-width: 0 2px 2px 0;
        top: 2px;
        left: 8px;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
        height: 15px;
        width: 7px; }
    .list-item-title {
      display: inline-block;
      width: 90%; }
    .list-item__input {
      background: #17181c;
      color: white;
      border: none;
      outline: none;
      font-size: 20px;
      height: 26px;
      width: 210px; }
    .list-item__delete {
      position: absolute;
      right: 0;
      top: 0;
      padding: 6px 12px; }
      .list-item__delete:hover {
        background: #474747;
        color: red; }

.checked {
  background: #474747;
  text-decoration: line-through; }
  .checked:hover {
    background: #474747; }
  .checked::before {
    content: "";
    position: absolute;
    border-color: #00e93a;
    border-style: solid;
    border-width: 0 2px 2px 0;
    top: 8px;
    left: 15px;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    height: 15px;
    width: 7px; }

.check {
  text-decoration: line-through;
  color: gray; }

.commentapp {
  max-width: 500px;
  width: 100%;
  margin: 10px auto;
  font-size: 14px;
  background-color: #17181c;
  border: 1px solid gray;
  padding: 20px; }
  .commentapp-header {
    background-color: #17181c;
    border: 1px solid gray;
    padding: 20px;
    margin-bottom: 10px; }
  .commentapp-field {
    margin-bottom: 15px;
    display: flex; }
    .commentapp-field-name {
      display: flex;
      flex-basis: 100px;
      font-size: 14px; }
    .commentapp-field-input input,
    .commentapp-field-input textarea {
      border: 1px solid gray;
      border-radius: 3px;
      padding: 5px;
      outline: none;
      font-size: 14px;
      resize: none;
      background-color: #17181c;
      color: white; }
    .commentapp-field-button {
      display: flex;
      justify-content: center; }
      .commentapp-field-button button {
        padding: 5px 10px;
        width: 80px;
        border: none;
        border-radius: 3px;
        background-color: #00a3cf;
        color: #fff;
        outline: none;
        cursor: pointer; }
        .commentapp-field-button button :active {
          background: #13c1f1; }

.comment {
  display: flex;
  border-bottom: 1px solid gray;
  margin-bottom: 10px;
  min-height: 30px; }
  .comment span {
    color: #00a3cf;
    font-style: italic; }
  .comment p {
    margin: 0;
    width: 69%; }
  .comment-createdtime {
    padding: 9px 9px 0px; }
  .comment-delete {
    cursor: pointer; }

